@font-face {
    font-family: 'sohne';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

/* HTML ELEMENTS STYLING */
:root{
    --color: #9e88fd;
    --place: #957eff;
}

body{
    margin: 0;
    padding: 0;
    font-family: sohne;
    background-color: black;
}
button{
    height: 26px;
    border-radius: 3px;

    font-size: 16px;
    cursor: pointer;

    background-color: #d9d4ff;
    color: #511dba;
}

button:hover{
    border-color: #511dba;
    background-color: #511dba;
    color: #d9d4ff;
}

button:hover:disabled{
    color: #d9d4ff; 
}

button:disabled{    
    cursor: not-allowed;
    border-color: #7776bb;
    background-color: #7776bb;
    color: #d9d4ff; 


}

/* HEADER STYLING */
.header{
    text-align: center;
    color: var(--color);
}

/* FOOTER STYLING  */
.footer{

    position: absolute;
    z-index: -1;
    color: var(--color);
    bottom: 0;

    left: 0; 
    right: 0; 
    margin-inline: auto; 
    width: fit-content;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    
}

/* GAME CONTAINER STYLING */
.game-container{
    display: flex;
    justify-content: center ;
    align-items: center;
}

/* DASHBOARD CONTAINER STYLING */
.dashboard{
    display: none;
    flex-direction: column;
    gap: 20px;
}

.dashboard-container{
    display: none;
    flex-direction: column;
    gap: 16px;
    
    width: 30vh;
    height: 30vh;
    margin-top: 7vh;
    margin-right: 10vh;
    
    padding: 10px;
    padding-left: 20px;
    border: 2px solid var(--color);
    border-radius: 6px;
    overflow-y: auto;
}


.dashboard-container > h4{
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--place);
} 

.ship-container{
    display: flex;
    flex-direction: row;
    width: fit-content;
    margin-left: 2vh;
    margin-bottom: 10px;
}

#reset-btn,
#random-btn,
#continue-btn{
    width: 33.5vh;
    height: 26px;
    border-radius: 3px;
}

#random-btn,
#continue-btn{
    display: none;
}

.ship-cell{
    background-color: #957eff;
}


/* SCROLLBAR STYLING */
::-webkit-scrollbar {
    width: 6px;
  }
  
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #78719f;
    border-radius: 6px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #d9d4ff;
    border-radius: 6px;
  }



/* BOARD CONTAINER STYLING */
.board-container{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;

    padding: 40px;
    margin-top: 10vh;
 
}

.player-one-board,
.player-two-board{
    display: grid;
    margin-right: 4vh;
    width: 400px;
    height: 400px;
    border: 6px solid var(--color);
}

.player-two{
    display: none;
}

.player-two-board{
    margin-right: 0;
}

.player-one > h4,
.player-two > h4{
    width: 94%;
    text-align: center;
    clear: left;
    color: var(--color);
}

.board-cell{
    width: 38px;
    height: 38px;
    border: 1px solid var(--color);
}
.no-ships-message{
    display: none;
    color: #957eff;
    text-align: center;
    margin-top: 50%;
}

.placed-ship{
    background-color: var(--place);
}

.attacked-ship{
    background-color: red;
}

.missed-attack{
    background-color: #3130d2;
}

/* TIMEOUT SCREEN STYLING */
.timeout-screen{
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: var(--color);

    position: fixed;
    z-index: 100;
    width: 100%;
    height: 100%;

    top: 0;
    left: 0;

    backdrop-filter: blur(100px);
}

#continue-game-btn{
    width: calc(100% - 86%);
    height: 30px;
}

/* STARTER MENU SCREEN STYLING */
.starter-screen{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 16vh;
}

.starter-screen-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #9e88fd;
}   

.game-mode-buttons{
    display: flex;
    gap: 20px;
}

.game-mode-buttons > button{
    width: 290px;
    height: 30px;
}

/* GAME OVER MODAL STYLING */
.game-over-modal{
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color);

    position: absolute;
    width: 100%;
    height: 100%;

    top: 0;
    left: 0;

    backdrop-filter: blur(100px);
}

.game-over-modal-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


#play-again-btn{
    width: 24vh;
    height: 25px;
}


/* GITHUB ICON STYLING (FOOTER) */
#github-icon:hover{
    animation: rotateIcon 1s ease-in-out;
}

@keyframes rotateIcon {

    100% {
        transform: scale(1.1);
        transform: rotate(-360deg);
    }

  }



/* STYLE PROPERTIES FOR DEVICES UPTO A WIDTH OF 1120px */
@media (max-width: 1120px) {

    /* BODY STYLING */
    body {
        font-size: 14px;
    }

    /* HEADER AND FOOTER STYLING */
    .header{
        text-align: center;
        color: var(--color);
    }

    .footer {
        position: absolute;
        z-index: 1;
        bottom: -20vh;
        width: 100%;
    }
    /* STARTER MENU SCREEN STYLING */
    .starter-screen-container{
        margin-top: 0 auto;
    }

    .starter-screen-container > h1{
        font-size: 20px;
        text-align: center;
    }

    .starter-screen-container > .game-mode-buttons{
        flex-direction: column;
        width: 70%;
    }

    .game-mode-buttons > button {
        width: 100%;
    }

    /* DASHBOARD CONTAINER STYLING */
    .dashboard-container {
        height: auto;
        margin-top: 5vh;
        gap: 16px;
    }
    
    /* GAME AND BOARD CONTAINER STYLING */
    .game-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 5vh;
        margin-bottom: 100px;
    }


    .board-container {
        display: flex;
        justify-self: center;
        align-items: center;
        flex-direction: column;
        gap: 20px;
        padding: 0px;
        margin-top: 10vh;
    }

    .player-one-board,
    .player-two-board {
        width: 270px;
        height: 270px;
        margin: auto;
        margin-bottom: 5vh;text-align: center;
    }

    .player-one > h4,
    .player-two > h4{
        width: 100%;
        text-align: center;
        clear: left;
        color: var(--color);
    }


    .board-cell {
        width: 25px;
        height: 25px;
    }

    /* GAME OVER MODAL STYLING */
    .game-over-modal {
        padding: 10px;
    }

    /* BUTTON STYLING */
    #continue-game-btn{
        width: 20vh;
    }

    #reset-btn,
    #play-again-btn {
        width: 112%;
        height: 35px;
    }


    #random-btn,
    #continue-btn{
        width: 17rem;
        margin: auto;
    }
}


/* FOOTER STYLING FOR DEVICES WITH DIFFERENT WIDTHS AND HEIGHTS */
@media (max-height: 600px) and (max-width: 950px){
    .footer{
        position: relative;
        bottom: -100px;
    }
}

@media (min-height: 300px) and (max-width: 800px){
    .footer{
        position: relative;
        bottom: -100px;
    }
}

@media (min-width: 900px) and (max-height: 900px) {
    .footer{
        position: relative;
        bottom: -50vh;
    }

    .dashboard-container{
        width: 30vh;
        height: 30vh;
    }

    #reset-btn{
        width: calc(100% - 24%);
    }

    #continue-btn{
        width: calc(100% - 4    );
    }

}





