 height: 100%;\n\n    top: 0;\n    left: 0;\n\n    backdrop-filter: blur(100px);\n}\n\n.game-over-modal-container{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n}\n\n\n#play-again-btn{\n    width: 24vh;\n    height: 25px;\n}\n\n\n/* GITHUB ICON STYLING (FOOTER) */\n#github-icon:hover{\n    animation: rotateIcon 1s ease-in-out;\n}\n\n@keyframes rotateIcon {\n\n    100% {\n        transform: scale(1.1);\n        transform: rotate(-360deg);\n    }\n\n  }\n\n\n\n/* STYLE PROPERTIES FOR DEVICES UPTO A WIDTH OF 1120px */\n@media (max-width: 1120px) {\n\n    /* BODY STYLING */\n    body {\n        font-size: 14px;\n    }\n\n    /* HEADER AND FOOTER STYLING */\n    .header{\n        text-align: center;\n        color: var(--color);\n    }\n\n    .footer {\n        position: absolute;\n        z-index: 1;\n        bottom: -20vh;\n        width: 100%;\n    }\n    /* STARTER MENU SCREEN STYLING */\n    .starter-screen-container{\n        margin-top: 0 auto;\n    }\n\n    .starter-screen-container > h1{\n        font-size: 20px;\n        text-align: center;\n    }\n\n    .starter-screen-container > .game-mode-buttons{\n        flex-direction: column;\n        width: 70%;\n    }\n\n    .game-mode-buttons > button {\n        width: 100%;\n    }\n\n    /* DASHBOARD CONTAINER STYLING */\n    .dashboard-container {\n        height: auto;\n        margin-top: 5vh;\n        gap: 16px;\n    }\n    \n    /* GAME AND BOARD CONTAINER STYLING */\n    .game-container {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        flex-direction: column;\n        margin-top: 5vh;\n        margin-bottom: 100px;\n    }\n\n\n    .board-container {\n        display: flex;\n        justify-self: center;\n        align-items: center;\n        flex-direction: column;\n        gap: 20px;\n        padding: 0px;\n        margin-top: 10vh;\n    }\n\n    .player-one-board,\n    .player-two-board {\n        width: 270px;\n        height: 270px;\n        margin: auto;\n        margin-bottom: 5vh;text-align: center;\n    }\n\n    .player-one > h4,\n    .player-two > h4{\n        width: 100%;\n        text-align: center;\n        clear: left;\n        color: var(--color);\n    }\n\n\n    .board-cell {\n        width: 25px;\n        height: 25px;\n    }\n\n    /* GAME OVER MODAL STYLING */\n    .game-over-modal {\n        padding: 10px;\n    }\n\n    /* BUTTON STYLING */\n    #continue-game-btn{\n        width: 20vh;\n    }\n\n    #reset-btn,\n    #play-again-btn {\n        width: 112%;\n        height: 35px;\n    }\n\n\n    #random-btn,\n    #continue-btn{\n        width: 17rem;\n        margin: auto;\n    }\n}\n\n\n/* FOOTER STYLING FOR DEVICES WITH DIFFERENT WIDTHS AND HEIGHTS */\n@media (max-height: 600px) and (max-width: 950px){\n    .footer{\n        position: relative;\n        bottom: -100px;\n    }\n}\n\n@media (min-height: 300px) and (max-width: 800px){\n    .footer{\n        position: relative;\n        bottom: -100px;\n    }\n}\n\n@media (min-width: 900px) and (max-height: 900px) {\n    .footer{\n        position: relative;\n        bottom: -50vh;\n    }\n\n    .dashboard-container{\n        width: 30vh;\n        height: 30vh;\n    }\n\n    #reset-btn{\n        width: calc(100% - 24%);\n    }\n\n    #continue-btn{\n        width: calc(100% - 4    );\n    }\n\n}\n\n\n\n\n\n"],"sourceRoot":""}]);
